h1{
	font-family: $heading__font;
	font-weight: 500;
}

h2,h3,h4,h5,h6{
	font-family: $body__font;
	font-weight: 500;
// 	text-transform: uppercase;
}

h1 {
    font-size: 25px;
    line-height: 30px;
}

h2 {
    font-size: 22px;
    line-height: 30px;
}

h3 {
    font-size: 18px;
    line-height: 20px;
}

h4 {
    font-size: 16px;
    line-height: 20px;
}

h5 {
    font-size: 14px;
    line-height: 20px;
}

h6 {
    font-size: 14px;
    line-height: 20px;
}

@include for-580-up{
	h1 {
	    font-size: 36px;
	    line-height: 40px;
	}
	
	h2 {
	    font-size: 25px;
	    line-height: 30px;
	}
}

p{
	font-size: 14px;
    line-height: 20px;
    font-weight: 400;
}

i{
	font-style: italic;
}

b{
	font-weight: 500;
}

/*
