	display: block;
	background-color: rgba(247,247,247,1);
	padding: 20px;
	border-radius: 5px;
}
*/

hr{
	border: none;
	border-bottom: 1px solid $border__color;
	margin: 20px 0px;
}

.text--margin-top{
	margin-top: 20px;
}

.text--margin-bottom{
	margin-bottom: 20px;
}

.text--margin-top-b{
	margin-top: 40px;
}

.text--margin-bottom-b{
	margin-bottom: 40px;
}

.text--left{
	text-align: left;
}

.text--right{
	text-align: right;
}

.text--center{
	text-align: center;
}

.text--shadow{
	text-shadow: 0px 32px 32px rgba(20, 34, 44, 0.20), 0px 16px 16px rgba(20, 34, 44, 0.20), 0px 8px 8px rgba(20, 34, 44, 0.20), 0px 4px 4px rgba(20, 34, 44, 0.20), 0px 2px 2px rgba(20, 34, 44, 0.20);
}

.text--yellow{
	color: $main__color;
}


