.alert{
	font-size: 12px;
	margin: 5px 0px;
	padding: 5px 0px;
	text-align: center;
	border-radius: 3px;
	
	&.alert--danger{
		color: $error__color;
		border: 1px solid $error__color;
	}
	
	&.alert--success{
		color: $succes__color;
		border: 1px solid $succes__color;
	}
	
	&.alert--info{
		color: $body__f-color;
		border: 1px solid $body__f-color;
	}
	
	&.alert--warning{
		color: $warning__color;
		border: 1px solid $warning__color;
	}
}


