.btn{
	position: relative;
	text-decoration: none;
	text-align: center;
	display: inline-block;
	padding: 7px 15px;
	background-color: $main__color;
	border: 2px solid $main__color;
	color: $white;
	border-radius: 5px;
	font-weight: 500;
	font-size: 14px;
	box-sizing: border-box;
	transition:all 0.75s cubic-bezier(0.23, 1, 0.32, 1);
	-webkit-transition:all 0.75s cubic-bezier(0.23, 1, 0.32, 1);
	-moz-transition:all 0.75s cubic-bezier(0.23, 1, 0.32, 1);
	-o-transition:all 0.75s cubic-bezier(0.23, 1, 0.32, 1);
	
	i{
		position: absolute;
		top: 50%;
		-webkit-transform: translateY(-50%); 
		-ms-transform: translateY(-50%);
		transform: translateY(-50%);
		left: 13px;
		width: 16px;
		height: 16px;
		display: block;
		margin-right: 13px;
		transition:all 0.75s cubic-bezier(0.23, 1, 0.32, 1);
		-webkit-transition:all 0.75s cubic-bezier(0.23, 1, 0.32, 1);
		-moz-transition:all 0.75s cubic-bezier(0.23, 1, 0.32, 1);
		-o-transition:all 0.75s cubic-bezier(0.23, 1, 0.32, 1);
	}
	
	&:hover{
		background-color: darken($main__color, 5%);
		border: 2px solid darken($main__color, 5%);
	}
}

.btn--icon{
	padding-left: 40px;
}

.btn--outline{
	background-color: transparent;
	border: 2px solid $main__color;
	color: $main__color;
	&:hover{
		background-color: $main__color;
		border: 2px solid $main__color;
		color: $white !important;
	}
}

.btn--inverted{
	background-color: transparent;
	border: 2px solid $white;
	color: $white;
	&:hover{
		background-color: $white;
		border: 2px solid $white;
		color: $main__color !important;
	}
}

.btn--cta{
	background-color: transparent;
	border: 2px solid $border__color;
	color: $body__f-color;
	&:hover{
		background-color: $main__color;
		border: 2px solid $main__color;
		color: $white !important;
	}
}

.btn--error{
	background-color: $error__color;
	border: 2px solid $error__color;
	color: $white;
}

.btn--error:hover{
	background-color: darken($error__color, 10%);
	border: 2px solid darken($error__color, 10%);
	color: $white !important;
}

.btn--succes{
	background-color: $succes__color;
	border: 2px solid $succes__color;
	color: $white;
}

.btn--succes:hover{
	background-color: darken($succes__color, 10%);
	border: 2px solid darken($succes__color, 10%);
	color: $white !important;
}

.btn--warning{
	background-color: $warning__color;
	border: 2px solid $warning__color;
	color: $white;
}

.btn--warning:hover{
	background-color: darken($warning__color, 10%);
	border: 2px solid darken($warning__color, 10%);
	color: $white;
}

.btn:hover > i{
	color: $white;
}

.btn--outline i{
	color: $body__f-light-l;
}

.btn--fb{
	background-color: $fb__color;
	border: 2px solid $fb__color;
	color: $white;
}

.btn--fb:hover{
	background-color: darken($fb__color, 10%);
	border: 2px solid darken($fb__color, 10%);
	color: $white;
}

.btn--google{
	background-color: $google__color;
	border: 2px solid $google__color;
	color: $white;
}

.btn--google:hover{
	background-color: darken($google__color, 10%);
	border: 2px solid darken($google__color, 10%);
	color: $white;
}

.btn--disabled{
	background-color: transparent;
	border: 2px solid $border__color;
	color: $border__color;
	cursor: not-allowed;
}

.btn--disabled:hover{
	background-color: transparent;
	border: 2px solid $border__color;
	color: $border__color;
	cursor: not-allowed;
}

.link{
	text-decoration: none;
	cursor: pointer;
	color: $body__f-color;
	transition:all 0.75s cubic-bezier(0.23, 1, 0.32, 1);
	-webkit-transition:all 0.75s cubic-bezier(0.23, 1, 0.32, 1);
	-moz-transition:all 0.75s cubic-bezier(0.23, 1, 0.32, 1);
	-o-transition:all 0.75s cubic-bezier(0.23, 1, 0.32, 1);
	
	&:hover{
		opacity: 0.7;
	}
}

.link--highlight{
	color: $main__color;
}


