  font-weight: normal;
  font-style: normal;
}

[class^="icon-"],
[class*=" icon-"] {
  position: relative;
  font-family: 'Simple-Line-Icons-Pro';
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

[class^="icon-"] span,
[class*=" icon-"] span {
  display: block;
  position: absolute;
  top: -0.3em;
  right: -0.6em;
  font-size: 0.5em;
  border-radius: 5em;
  text-align: center;
  height: 1.3em;
  width: 1.3em;
  line-height: 1.2em;
  background-color: #e82e60;
  color: white;
  font-family: 'Affogato';
  font-weight: 500;
}

